<template lang="pug">
  #sign.flex
    .flex-stretch
      .features
        h1.features-title Оптовые закупки качественных нефтепродуктов по лучшим ценам
        .feature(v-for="(icon, index) in features").flex
          .feature-icon.flex-thin.flex-center
            icon.icon(:name="icon.name" :width="icon.size" :heigth="icon.size")
          .feature-info.flex-stretch
            .feature-title {{ $t(`home.features.${index + 1}.title`) }}
            .feature-text {{ $t(`home.features.${index + 1}.text`) }}

    .flex-thin.sign
      sign-component(v-model="tab")
</template>

<script>
import signComponent from '@/components/sign'

export default {
  name: 'sign',
  components: {signComponent},

  data: () => ({
    tab: 1,
    features: [
      {name: 'chart', size: 32},
      {name: 'case', size: 38},
      {name: 'oil', size: 35},
    ],
  }),

  mounted() {
    this.setTab()
  },

  methods: {
    setTab() {
      if (typeof this.$route.meta.tab !== 'undefined') {
        this.tab = this.$route.meta.tab
      }
    },
  },

  watch: {
    $route() {
      this.setTab()
    }
  },
}
</script>

