<template lang="pug">
  .login-by-sber
    form.form(@submit.prevent="submit")
      form-field(
        :label="$t('sign.sberID')"
        :info="$t('sign.sberID')"
        :errors="errors.sberID"
      )
        form-input(
          v-model="sberID"
          @input="errors.sberID && errors.sberID.length ? validateOne('sberID', rules.sberID) : null"
          @blur="validateOne('sberID', rules.sberID)"
        )
      form-field(
        :label="$t('sign.password')"
        :info="$t('sign.password')"
        :errors="errors.password"
      )
        .form-addons
          .form-addon(@click="isShowPassword = !isShowPassword")
            icon(:name="isShowPassword ? 'eye1' : 'eye2'")
        form-input(
          v-model="password"
          :type="isShowPassword ? 'text' : 'password'"
          :addonsCount="1"
          @input="errors.password && errors.password.length ? validateOne('password', rules.password) : null"
          @blur="validateOne('password', rules.password)"
        )
      .login-options
        .forgot
          a {{ $t('sign.forgot') }}
      btn(big primary wide type="submit" :loading="isLoading" :disabled="isLoading") {{ $t('sign.loginAction') }}
</template>

<script>
import {required} from '@/utilities/validators'
import validateMixin from '@/utilities/validateMixin'
import {mapActions} from 'vuex'

export default {
  name: 'loginBySber',
  mixins: [validateMixin],

  data() {
    return {
      sberID: '',
      password: '',
      isShowPassword: false,
      isLoading: false,
      rules: {
        sberID: [required],
        password: [required],
      },
    }
  },

  methods: {
    ...mapActions('auth', ['unlock']),
    submit() {
      if (this.validateAll(this.rules)) {
        this.send({sberID: this.sberID, password: this.password})
      }
    },
    async send(data) {
      this.isLoading = true
      // this.$API.post('loginBySber', data)
      const fake$API = ms => new Promise(resolve => {
        setTimeout(resolve, ms, {
          token: 'Bearer Lorem ipsum dolor sit amet, consectetur adipisicing elit.'
        })
      })
      try {
        const result = await fake$API(1000, data)
        this.unlock(result)
      } finally {
        this.isLoading = false
      }
    },
  },
}
</script>

<style lang="sass">
.login-options
  padding-top: 3px
  padding-right: 3px
  display: flex
  justify-content: space-between
  margin-bottom: 31px
</style>
