import i18n from '@/translations/i18n'

export const required = (value) => {
  let isValid = true
  if (Array.isArray(value)) {
    if (!value || (value && !value.length)) isValid = false
  } else {
    if (!value) isValid = false
  }
  return isValid || i18n.t('validators.required')
}

export const email = (value) => value ? /\S*@\S*\.\S*[^.]$/.test(value) || i18n.t('validators.email') : true

export default { required, email }
