<template lang="pug">
  .login-by-phone
    form.form(@submit.prevent="submit")
      form-field(
        :label="$t('sign.phone')"
        :info="$t('sign.phone')"
        :errors="errors.phone"
      )
        form-input(
          v-model="phone"
          type="number"
          @input="errors.phone && errors.phone.length ? validateOne('phone', rules.phone) : null"
          @blur="validateOne('phone', rules.phone)"
        )
      .login-options
        .remember
          check {{ $t('sign.remember') }}
      btn(big primary wide type="submit" :loading="isLoading" :disabled="isLoading") {{ $t('sign.loginAction') }}
</template>

<script>
import {required} from '@/utilities/validators'
import validateMixin from '@/utilities/validateMixin'
import {mapActions} from 'vuex'

export default {
  name: 'loginByPhone',
  mixins: [validateMixin],

  data() {
    return {
      phone: '',
      isLoading: false,
      rules: {
        phone: [required],
      },
    }
  },

  methods: {
    ...mapActions('auth', ['unlock']),
    submit() {
      if (this.validateAll(this.rules)) {
        this.send({phone: this.phone})
      }
    },
    async send(data) {
      this.isLoading = true
      // this.$API.post('loginByPhone', data)
      const fake$API = ms => new Promise(resolve => {
        setTimeout(resolve, ms, {
          token: 'Bearer Lorem ipsum dolor sit amet, consectetur adipisicing elit.'
        })
      })
      try {
        const result = await fake$API(1000, data)
        this.unlock(result)
      } finally {
        this.isLoading = false
      }
    },
  },
}
</script>

<style lang="sass">
.login-options
  padding-top: 3px
  padding-right: 3px
  display: flex
  justify-content: space-between
  margin-bottom: 31px
</style>
