export default {
  data() {
    return {
      errors: {},
    }
  },

  methods: {
    validateOne(fieldName, rules) {
      let isValid = true
      this.$set(this.errors, fieldName, [])
      for (const rule of rules) {
        const message = rule(this[fieldName])
        if (typeof message === 'string') {
          isValid = false
          this.errors[fieldName].push(message)
        }
      }
      return isValid
    },
    validateAll(rules) {
      let isValid = true
      for (const key in rules) {
        if (!this.validateOne(key, rules[key])) isValid = false
      }
      return isValid
    },
  },
}
