<template lang="pug">
  .login
    login-by-email(v-if="activeForm === 'email'")
    login-by-sber(v-if="activeForm === 'sber'")
    login-by-phone(v-if="activeForm === 'phone'")
    .others-title  {{ $t('sign.others') }}
    .others
      .other-login(v-if="activeForm !== 'email'")
        btn(@click="activeForm = 'email'" wide) {{ $t('sign.emailBtn') }}
      .other-login(v-if="activeForm !== 'sber'")
        btn(@click="activeForm = 'sber'" wide) {{ $t('sign.sberBtn') }}
      .other-login(v-if="activeForm !== 'phone'")
        btn(@click="activeForm = 'phone'" wide) {{ $t('sign.phoneBtn') }}

</template>

<script>
import loginByEmail from '@/components/loginByEmail'
import loginBySber from '@/components/loginBySber'
import loginByPhone from '@/components/loginByPhone'
export default {
  name: 'login',
  components: {loginByEmail, loginBySber, loginByPhone},

  data: () => ({
    activeForm: 'email',
  }),
}
</script>

<style lang="sass">
.others
  display: flex
  flex-wrap: wrap
  &-title
    margin-top: 35px
    margin-bottom: 23px
    text-align: center
    font-size: 16px
.other-login
  flex: 1 1 0
  &:nth-child(0)
    margin-left: 10px
  &:nth-child(1)
    margin-right: 10px
</style>
