<template lang="pug">
  .sign.flex-column
    tabs.flex-thin(v-model="tab" :list="tabs")
    .sign-container.flex-stretch
      login(v-if="!tab")
</template>

<script>
import login from '@/components/login'

export default {
  name: 'sign-component',
  components: {login},

  props: {
    value: {type: Number, default: 1},
  },

  data() {
    return {
      tabs: [
        this.$t('sign.login'),
        this.$t('sign.registration'),
      ],
    }
  },

  computed: {
    tab: {
      get() { return this.value },
      set(value) { this.$router.push(value ? '/registration' : '/sign-in') },
    },
  },
}
</script>

<style lang="sass">
.sign
  &-container
    padding: 40px 34px 34px 34px
    background: $white1
</style>
